.player{
    width: 100vw;
    height: 100vh;
}

.player__body{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
}

.player__footer{
    position: fixed;
    bottom: 1rem;
    z-index: 1000;
    left: 0;
    width: 96%;
    margin: 0 2%;
    height: auto;
    padding: 2rem;
    background-color: #282828;
    height: 7rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}


