.login{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}

.login__logo{
    height: 100%;
    width: 100%;
    -o-object-fit: contain;
       object-fit: contain;
}

.login__button{
    padding: 20px;
    border-radius: 100px;
    font-weight: 800;
    color: white;
    text-decoration: none;
    background-color: #1db954;
    font-size: 1.6rem;
}


