.footer__left{
    -ms-flex: 0.3 1;
        flex: 0.3 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    max-width: 300px;
    overflow: hidden;
}

.footer__left > img{
    margin-right: 20px;
    height: 60px;
    width: 60px;
    -o-object-fit: contain;
       object-fit: contain;
}

.footer__left > div > h4{
    margin-bottom: 0.5rem;
    font-size: 1.4rem;
}

.footer__left > div > p{
    margin-bottom: 0.5rem;
    font-size: 1rem;
}

.footer__center{
    -ms-flex: 0.4 1;
        flex: 0.4 1;
    color: white;
    padding: 0 5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    max-width: 300px;
}

.footer__right{
    -ms-flex: 0.3 1;
        flex: 0.3 1;
    
}

.footer__right__container{
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
}

.footer__green{
    color: #1ed15e;
}

.footer__green,
.footer__icon{
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.footer__green:hover,
.footer__icon:hover{
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    -webkit-transform: scale(1.2) !important;
        -ms-transform: scale(1.2) !important;
            transform: scale(1.2) !important;
}

.footer__green:active,
.footer__icon:active{
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    -webkit-transform: scale(1) !important;
        -ms-transform: scale(1) !important;
            transform: scale(1) !important;
}


