.header{
    display: -ms-flexbox;
    display: flex;
}

.header__left{
    -ms-flex: 0.5 1;
        flex: 0.5 1;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-radius: 30px;
    padding: 10px;
    min-width: 70px;
}

.header__left__input{
    width: 100%;
    border: none;
    outline: none;
    font-family: inherit;
}

.header__left__icon{
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    color: grey;
    margin: 0 0.5rem;
}

.header__left__icon:hover{
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.header__right{
    -ms-flex: 0.5 1;
        flex: 0.5 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.header__right__avatar{
    margin-right: 0.8rem;
}

.header__right__name{
    background-image: -webkit-gradient(linear,left bottom, left top,from(rgb(243, 243, 243)),to(rgb(23, 190, 23)));
    background-image: -webkit-linear-gradient(bottom,rgb(243, 243, 243),rgb(23, 190, 23));
    background-image: -o-linear-gradient(bottom,rgb(243, 243, 243),rgb(23, 190, 23));
    background-image: linear-gradient(to top,rgb(243, 243, 243),rgb(23, 190, 23));
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-size: 1.2rem;
}


