.sidebarOptions{
    font-size: 1rem;
    color: grey;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    font-family: none;
    height: 40px;
}

.sidebarOptions:hover{
    color: white;
}

.sidebarOptions__icon{
    margin: 0 10px;
}

.sidebarOptions > p{
    margin: 10px 0px 0px 10px;
    font-size: 1rem;
}


