.body{
    -ms-flex: 80% 1;
        flex: 80% 1;
    background: -webkit-gradient(linear,left top, left bottom,from(rgb(91,87,115)),to(rgba(0,0,0,1)));
    background: -webkit-linear-gradient(rgb(91,87,115),rgba(0,0,0,1));
    background: -o-linear-gradient(rgb(91,87,115),rgba(0,0,0,1));
    background: linear-gradient(rgb(91,87,115),rgba(0,0,0,1));
    padding: 2rem;
    color: white;
    height: 100vh;
    padding-bottom: 8rem;
    overflow-y: overlay;
    z-index: 100;
}

.body::-webkit-scrollbar{
    display: none;
}

.body__info{
    margin-top: 2rem;
    display: -ms-flexbox;
    display: flex;
    padding: 0 20px;
    -ms-flex-align: end;
        align-items: flex-end;
}

.body__info__image{
    -o-object-fit: contain;
       object-fit: contain;
    height: 20vw;
    margin: 0 20px;
    -webkit-box-shadow:  0 4px 60px 
                rgba(0,0,0,1);
            box-shadow:  0 4px 60px 
                rgba(0,0,0,1);
}

.body__info__text{
    -ms-flex: 1 1;
        flex: 1 1;
}

.body__info__text > strong{
    font-size: 1.4rem;
}

.body__info__text > h4{
    font-size: 4.8rem;
    margin: 1rem 0;
}

.body__info__text > p{
    font-size: 1.2rem;
}

.body__songs{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.body__songs__icon{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 50px 0 20px 20px;
}

.body__songs__icon > .MuiSvgIcon-root{
    margin-right: 2rem;
}

.body__shuffle{
    font-size: 8rem !important;
    -webkit-transition: 100ms all ease-in-out;
    -o-transition: 100ms all ease-in-out;
    transition: 100ms all ease-in-out;
}

.body__shuffle:hover{
    -webkit-transition: 100ms all ease-in-out;
    -o-transition: 100ms all ease-in-out;
    transition: 100ms all ease-in-out;
    -webkit-transform: scale(1.08);
        -ms-transform: scale(1.08);
            transform: scale(1.08);
}



